import config from "config/config.js";

export default {
	data() {
		return {
			username: '',
			password: '',
			flag:false,
			agreement:'<<服务协议>>'
		}
	},
	methods: {
		
		toAgreement(){
			uni.navigateTo({
				url:'agreement'
			})
		},
		
		
		agreementBtn(e){
			var temp = e.detail.value;
			if(temp.length != 0){
				this.flag = true;
				console.log("flag:",this.flag)
			}
			else{
				this.flag = false
				console.log("flag:",this.flag)
			}
			
		},
		
		gotoregist() {
			uni.navigateTo({
				url: "./regist"
			})
		},
		loginrequest() {
			var that = this;

			if (this.username == '' || this.password == '') {
				uni.showModal({
					title: '提示',
					content: '请输入用户名或密码',
					showCancel: false,
					confirmText: "确定"
				});
			} 
			else if (!this.flag) {
				uni.showModal({
					title: '提示',
					content: '请阅读并同意以下协议',
					showCancel: false,
					confirmText: "确定"
				});
			}
			else {
				uni.request({
					url: config.reUrl + '/FuStar/LoginVerify',
					data: {
						uname: this.username,
						upwd: this.password,
					},
					success: (res) => {
						console.log("LoginVerify", res.data);
						// console.log("typeof :" , typeof res.data);
						if (res.data) {
							getApp().globalData.userName = that.username;
							var allUsers = getApp().globalData.allUsers;
							for (var i = 0; i < allUsers.length; i++) {
								if (allUsers[i].uname == that.username) {
									getApp().globalData.userIconImg = allUsers[i].uicon;
								}
							}
							uni.showToast({
								title: '登入成功'
							});
							uni.switchTab({
								url: '../nearby/index'
							})

						} else {
							uni.showModal({
								title: '提示',
								content: '用户名或密码错误，请重新输入',
								showCancel: false,
								confirmText: "确定"
							});
						}
					}
				});
			}

		}
	}
}
